import axios from 'axios';
import logService from '../../services/log';
export default class Api {
    constructor() {
        const defaultOptions = {
            baseURL: 'https://reqres.in/api/'
        };
        this.httpClient = axios.create(defaultOptions);
        this.logSuccess = {'prefix': 'DATASERVICE', 'color': 'blue', 'enableLog': true};
        this.logFail = {'prefix': 'DATASERVICE', 'color': 'red', 'enableLog': true};
    }

    async get(url) {
        try {
            logService(`GET ${url}`, '', this.logSuccess);
            const response = await this.httpClient.get(url);
            logService(`GET ${url} >>> SUCCESS: `, response, this.logSuccess);
            return response? response.data : '';
        } catch(err) {
            logService(`GET ${url} >>> ERROR: `, err.message, this.logFail);
            return Promise.reject(err);
        }
    }

    async post(url, data) {
        try {
            logService(`POST ${url}`, data, this.logSuccess);
            const response = await this.httpClient.post(url, data);
            logService(`POST ${url} >>> SUCCESS: `, response, this.logSuccess);
            return response? response.data : '';
        } catch(err) {
            logService(`POST ${url} >>> ERROR: `, err.message, this.logFail);
            return Promise.reject(err);
        }
    }

    async put(url, data) {
        try {
            logService(`PUT ${url}`, data, this.logSuccess);
            const response = await this.httpClient.put(url, data);
            logService(`PUT ${url} >>> SUCCESS: `, response, this.logSuccess);
            return response? response.data : '';
        } catch(err) {
            logService(`PUT ${url} >>> ERROR: `, err.message, this.logFail);
            return Promise.reject(err);
        }
    }

    async delete(url) {
        try {
            logService(`DELETE ${url}`, '', this.logSuccess);
            const response = await this.httpClient.delete(url);
            return response? response.data : '';
        } catch(err) {
            logService(`DELETE ${url} >>> ERROR: `, err.message, this.logFail);
            return Promise.reject(err);
        }
    }
   
}