import actionTypes from './types';

const initialState = {
    isFetching: false,
    didInvalid: false,
    lastUpdated: 1439478405547,
    fetchedPageCount: 0,
    items: [],
    searchValue: ''
};

const friendsReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.REQUEST_USERS:
            return { ...state, isFetching: true };
        case actionTypes.RECEIVE_USERS:
            const dataUpdate = action.users && action.users.length > 0 ? action.users : state.items;
            return { ...state, isFetching: false, items: [...dataUpdate] };
        case actionTypes.ADD_USER:
            return { ...state, items: [...state.items, ...action.user] };
        default:
            return state;
    }
}


export default friendsReducer;