  /*global chrome*/
import React, {useEffect, useState} from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import './App.css';
import privateRoutes from './routes/privateRoutes';
import sessionRoutes from './routes/sessionRoutes';
import Dashboard from './layouts/dashboard';
import Session from './layouts/session';
import NotFound from './views/not-found';
import { useStore } from 'react-redux';
import logService from './services/log';
export default function App ()  {
    const store = useStore();
    useEffect(() => {
        store.subscribe(() => { logService.console('UPDATED: ', store.getState(), { 'prefix': 'STORE', 'color': 'green', 'enableLog': true }) });
    })
        return (
            <div className="App">
               <Switch>
                    {privateRoutes.map((route, key) => {
                        const { component, path } = route;
                        return (
                            <Route
                                exact
                                path={path}
                                key={key}
                                render={(route) =>
                                  <Dashboard component={component} route={route} />
                                }
                            />
                        )
                    })}
        
                    {sessionRoutes.map((route, key) => {
                        const { component, path } = route;
                        return (
                            <Route
                                exact
                                path={path}
                                key={key}
                                render={(route) => <Session component={component} route={route} />
                                }
                            />
                        )
                    })}
                    <Route component={NotFound} />
                </Switch>
            </div>
          );
}

