  /*global chrome*/
  import React, { useState, useEffect } from 'react';
  import usersApi from './api/users';
  import useAxiosAsync from './hooks/use-axios-async';

  export default function StartUp (props) {
    const [gender, setGender] = useState('');
    const { response, loading, error, reFetch } = useAxiosAsync({
      url: `https://randomuser.me/api/${gender === 'unknown' ? 'unknown' : ''}`,
      method: 'GET',
      options: {
        params: { gender },
      },
      trigger: gender
    });
    console.log(response);
    console.log(loading);
    console.log(error);
    return (
      <>
      {loading && (<p>loading...</p>)}
      <button onClick={e => reFetch()} style={{width: '100px', height: '100px', position: 'absolute', zIndex: 1000}}>retry</button>
       <div>{props.children}</div>
      </>
     
    )
  }
      
  